/* Reset CSS */
* {
  -webkit-text-size-adjust: none;
  box-sizing: border-box;
}

html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, abbr, address, cite, code, del, dfn, em, img, ins, kbd, q, samp, small, strong, sub, sup, var, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, figcaption, figure, footer, header, hgroup, menu, nav, section, summary, time, mark, audio, video, main {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
  display: block;
}

/* Main styles */
html {
  min-height: 100%;
  background: #bf6f43;
}

body {
  min-height: 100%;
  position: relative;
  background: #fdf5e6;
  font: 1rem/1.3 Arial, sans-serif;
  color: #222;
  min-width: 320px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  vertical-align: top;
  max-width: 100%;
  height: auto;
}

a {
  color: #222;
  -webkit-tap-highlight-color: transparent;
  text-decoration: none;
  transition: color 0.3s ease;
}
a:hover {
  color: #6667AB;
}

p {
  margin: 0 0 1rem;
}

button {
  cursor: pointer;
}

/* Titles fonts */
h1 {
  font-size: 2.75rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.5rem;
}

h1 mark,
h1 a,
h2 mark,
h2 a,
h3 mark,
h3 a {
  color: #6667AB;
  background: none;
  font-style: normal;
}

/* Main styles */
#wrapper {
  max-width: 100%;
  position: relative;
  min-height: 100%;
  overflow: hidden;
}

/* Header styles */
#header {
  padding: 10px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #cecece;
  background: #bf6f43;
}
#header nav > ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}
#header nav > ul > li {
  padding-left: 15px;
}
#header nav > ul > li > a {
  color: #fff;
}
#header .btn-menu {
  display: none;
}
#header .dropdown-item {
  font-size: 0.9375rem;
  padding: 0.5rem 1rem;
}
#header .dropdown-item:focus,
#header .dropdown-item:hover {
  background-color: #eee;
}
#header .dropdown-item:active {
  background-color: #bf6f43;
}

.logo-js {
  display: block;
  width: 200px;
  height: 36px;
  background: url("../images/logo-js.png") no-repeat;
  text-indent: -9999px;
  overflow: hidden;
}
.logo-js a {
  display: block;
  height: 100%;
}

/* Main content */
main {
  min-height: 50vh;
  padding: 40px 40px;
}

.block {
  margin-bottom: 1rem;
  border-top: 2px solid #bbb;
  background-color: #fff;
  border-radius: 10px;
  border-bottom: 2px solid #aaa;
  border-right: 1px solid #ddd;
  box-shadow: 2px 5px 5px 2px rgba(0, 0, 0, 0.1);
}
.block .heading h2 {
  padding: 0.5rem 1.5rem 0.5rem 1rem;
  font-size: 1rem;
  font-weight: bold;
  position: relative;
}
.block .heading h2:after {
  content: "";
  display: block;
  clear: both;
  position: absolute;
  right: 0.5rem;
  top: 50%;
  transform: translateY(-50%) rotate(0);
  width: 12px;
  height: 12px;
  background: url("../images/arrow-down-icon.svg") no-repeat 50% 50%;
  background-size: 12px;
  transition: all 0.3s ease;
}
.block .heading:not(.collapsed) h2:after {
  transform: translateY(-50%) rotate(-180deg);
}
.block .item {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 0.5rem 0.5rem 0.5rem 1rem;
}
.block .item:hover {
  background-color: rgba(237, 227, 207, 0.25);
}
.block .btn-icon {
  margin-left: 0.5rem;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1.5rem;
  height: 1.5rem;
  background-color: #bf6f43;
  color: #fff;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}

/* Footer */
#footer {
  padding: 10px 40px;
  border-top: 1px solid #cecece;
  background: #bf6f43;
  color: #fff;
}
#footer .box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#footer .nav {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}
#footer .nav li {
  padding-left: 15px;
}
#footer .nav a {
  color: #fff;
}
#footer .copyright {
  margin: 20px 0 0;
  text-align: center;
}